import React from "react";
import style from "./Profile.module.css";

import PageHeader from "../PageHeader/PageHeader";
import Username from "../../Common/User/Username/Username";
import Avatar from "../../Common/User/Avatar/Avatar";
import Bio from "../../Common/User/Bio/Bio";
import Time from "../../Common/Time/Time";
import Followers from "../../Common/User/Followers/Followers";
import Following from "../../Common/User/Following/Following";
import Sidebar from "../Sidebar/Sidebar";

import { NavLink } from "react-router-dom";
import { hideScrollBar } from "../../../js/hideScrollBar";

const Profile = (props) => {
  const showBackground = (elem) => {
    if (elem === undefined) {
      return (
        <div className={style.bgImage} style="background-color: #f5f8fa"></div>
      );
    } else {
      return (
        <div className={style.bgImage}>
          <img width="600px" height="200px" src={elem} alt />
        </div>
      );
    }
  };

  const showBio = (elem) => {
    if (elem !== undefined) {
      return (
        <div className={style.bio}>
          <Bio bio={elem} />
        </div>
      );
    }
  };

  return (
    <main className={style.container}>
      <div className={style.wrap}>
        <PageHeader title="Profile" intro={props.user.tweets} back="true" />
        <section className={style.content} onWheel={hideScrollBar}>
          <div className={style.bg_image} tabIndex="0">
            {showBackground(props.user.bgImage)}
          </div>
          <div className={style.content_wrap}>
            <div className={style.avatar} tabIndex="0">
              <Avatar avatar={props.user.avatar} />
            </div>
            <div className={style.setup_wrap}>
              <NavLink to="/setup_profile" className={style.setup}>
                Set up profile
              </NavLink>
            </div>
            <span className={style.name}>{props.user.name}</span>
            <Username username={props.user.username} />
            {showBio(props.user.bio)}
            <div className={style.joined}>
              <span className={style.joined_icon}>
                <svg viewBox="0 0 24 24">
                  <g>
                    <path d="M19.708 2H4.292C3.028 2 2 3.028 2 4.292v15.416C2 20.972 3.028 22 4.292 22h15.416C20.972 22 22 20.972 22 19.708V4.292C22 3.028 20.972 2 19.708 2zm.792 17.708c0 .437-.355.792-.792.792H4.292c-.437 0-.792-.355-.792-.792V6.418c0-.437.354-.79.79-.792h15.42c.436 0 .79.355.79.79V19.71z"></path>
                    <circle cx="7.032" cy="8.75" r="1.285"></circle>
                    <circle cx="7.032" cy="13.156" r="1.285"></circle>
                    <circle cx="16.968" cy="8.75" r="1.285"></circle>
                    <circle cx="16.968" cy="13.156" r="1.285"></circle>
                    <circle cx="12" cy="8.75" r="1.285"></circle>
                    <circle cx="12" cy="13.156" r="1.285"></circle>
                    <circle cx="7.032" cy="17.486" r="1.285"></circle>
                    <circle cx="12" cy="17.486" r="1.285"></circle>
                  </g>
                </svg>
              </span>
              Joined
              <div className={style.joined_wrap}>
                <Time date={props.user.joinedDate} />
              </div>
            </div>
            <div className={style.following}>
              <Following
                path={props.user.username}
                following={props.user.following}
              />
            </div>
            <div className={style.followers}>
              <Followers
                path={props.user.username}
                followers={props.user.followers}
              />
            </div>
          </div>
        </section>
      </div>
      <Sidebar users={props.users} />
    </main>
  );
};

export default Profile;
