import React from "react";
import style from "./MobilePage.module.css";
import { Route, BrowserRouter } from "react-router-dom";

import HomeContainer from "../Home/HomeContainer";
import ListsContainer from "../Lists/ListsContainer";
import NotificationsContainer from "../Notifications/NotificationsContainer";
import MessagesContainer from "../Messages/MessagesContainer";

import PageNav from "../PageNav/PageNav";

function MobilePage(props) {
  return (
    <BrowserRouter>
      <div className={style.container}>
        <Route
          path="/home"
          render={() => <HomeContainer store={props.store} />}
        />
        <Route
          path="/notifications"
          render={() => <NotificationsContainer store={props.store} />}
        />
        <Route
          path="/messages"
          render={() => <MessagesContainer store={props.store} />}
        />
        <Route
          path="/lists"
          render={() => <ListsContainer store={props.store} />}
        />
      </div>
      <footer className={style.footer}>
        <PageNav />
      </footer>
    </BrowserRouter>
  );
}

export default MobilePage;
