import React from "react";
import WriteArea from "./WriteArea";

import {
  updateMessageCreator,
  sendMessageCreator,
} from "../../../../redux/dialogsReducer";

const WriteAreaContainer = (props) => {
  const updateNewMessage = (text) => {
    props.dispatch(updateMessageCreator(text));
  };

  const sendNewMessage = (text) => {
    props.dispatch(sendMessageCreator(text));
  };

  return (
    <WriteArea
      updateNewMessage={updateNewMessage}
      sendNewMessage={sendNewMessage}
    />
  );
};

export default WriteAreaContainer;
