import React from "react";
import style from "./Messages.module.css";

import Search from "../../Common/Search/Search";
import DialogInfo from "../../Common/DialogInfo/DialogInfo";
import Dialog from "../../Common/Dialog/Dialog";

import { hideScrollBar } from "../../../js/hideScrollBar";
import PageHeader from "../PageHeader/PageHeader";

const Messages = (props) => {
  let dialogsArray = props.dialogs;

  let dialogsInfo = dialogsArray.map((elem) => <DialogInfo dialog={elem} />);

  return (
    <main className={style.container}>
      <div className={style.wrap}>
        <PageHeader title="Messages" />
        <section className={style.search}>
          <Search searchText="Search for people and groups" />
        </section>
        <section className={style.content} onWheel={hideScrollBar}>
          {dialogsInfo}
        </section>
      </div>
      <div className={style.dialog}>
        <Dialog
          dispatch={props.dispatch}
          value={props.newMessage}
          user={props.dialogs[0]}
        />
      </div>
    </main>
  );
};

export default Messages;
