import React from "react";
import "./App.css";

import DesktopPage from "./components/Desktop/DesktopPage/DesktopPage";
import MobilePage from "./components/Mobile/MobilePage/MobilePage";

function App(props) {
  console.log(props.isTouchDevice === true);
  if (props.isTouchDevice) {
    return <MobilePage store={props.store} />;
  } else {
    return <DesktopPage store={props.store} />;
  }
}

export default App;
