export const dialogsReducer = (state = initialState, action) => {
  switch (action.type) {
    case UPDATE_NEW_MESSAGE:
      state.newMessage.text = action.text;

      return state;

    case SEND_NEW_MESSAGE:
      state.dialogs[0].messages.push({
        type: "outgoing",
        text: action.text,
        date: Date.parse(new Date()),
      });

      return state;

    default:
      return state;
  }
};

const UPDATE_NEW_MESSAGE = "UPDATE_NEW_MESSSAGE";
const SEND_NEW_MESSAGE = "SEND_NEW_MESSAGE";

export const updateMessageCreator = (text) => {
  return { type: UPDATE_NEW_MESSAGE, text };
};

export const sendMessageCreator = (text) => {
  return { type: SEND_NEW_MESSAGE, text };
};
