import React from "react";
import style from "./Home.module.css";

import Post from "../../Common/Post/Post";
import Tweet from "../../Common/Tweet/Tweet";

import { hideScrollBar } from "../../../js/hideScrollBar";
import PageHeader from "../PageHeader/PageHeader";
import Sidebar from "../Sidebar/Sidebar";

const Home = (props) => {
  let posts = props.posts;
  let postsArray = posts.map((elem) => <Post posts={elem} />);

  return (
    <main className={style.container}>
      <div className={style.wrap}>
        <PageHeader title="Home" />
        <section className={style.content} onWheel={hideScrollBar}>
          <Tweet
            user={props.user}
            newPost={props.newPost}
            dispatch={props.dispatch}
          />
          {postsArray.reverse()}
        </section>
      </div>
      <Sidebar users={props.users} />
    </main>
  );
};

export default Home;
