const ADD_NEW_POST = "ADD-NEW-POST";
const UPDATE_NEW_POST = "UPDATE-NEW-POST";

export const postsReducer = (state = initialState, action) => {
  switch (action.type) {
    case ADD_NEW_POST:
      state.list.push({
        avatar: state.user.avatar,
        name: state.user.name,
        lastName: state.user.lastName,
        username: state.user.username,
        id: state.user.id,
        post: {
          id: Math.random() * 1000000,
          date: Date.parse(new Date()),
          text: state.newPost.text,
          image: state.newPost.image,
          comments: "0",
          reposts: "0",
          likes: "0",
        },
      });

      state.newPost.text = "";
      state.newPost.image = "";

      return state;

    case UPDATE_NEW_POST:
      state.newPost.text = action.text;
      state.newPost.image = action.image;

      return state;

    default:
      return state;
  }
};

export const addNewPostCreater = () => ({
  type: ADD_NEW_POST,
});

export const updateNewPostCreator = (text, image) => {
  return { type: UPDATE_NEW_POST, text, image };
};
