import React from "react";
import style from "./Time.module.css";

const Time = (props) => {
  const createDate = (param) => {
    let date = new Date(+param);
    let month = date.getMonth();
    let day = date.getDate();
    let monthArray = [
      "Jan",
      "Feb",
      "March",
      "Apr",
      "May",
      "Jun",
      "Jul",
      "Aug",
      "Sep",
      "Oct",
      "Nov",
      "Dec",
    ];
    return (
      <time className={style.time} dateTime={param}>
        {monthArray[month - 1]} {day}
      </time>
    );
  };

  return createDate(props.date);
};

export default Time;
